import React, { Component } from 'react'
import Link from 'gatsby-link'

const displayBlock = {
  display: 'block'
}

const displayNone = {
  display: 'none'
}

let pathPrefix = `/`;
if (typeof __PREFIX_PATHS__ !== `undefined` && __PREFIX_PATHS__) {
  pathPrefix = __PATH_PREFIX__
}

let wwidth = (typeof(window) === "undefined") ? 800 : window.innerWidth;

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toggleMenu: false,
      toggleMenuBg: false,
      windowWidth: wwidth
    };

    this.handleMenuClick = this.handleMenuClick.bind(this);
    this.handleBgClick = this.handleBgClick.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  handleMenuClick() {
    if(this.state.windowWidth > 767) {
      this.setState(
        {
          toggleMenu: true,
          toggleMenuBg: false,
        }
      );
    } else {
      this.setState(
        {
          toggleMenu: !this.state.toggleMenu,
          toggleMenuBg: !this.state.toggleMenuBg,
        }
      );
    }
  }

  handleBgClick() {
    this.setState(
      {
        toggleMenu: false,
        toggleMenuBg: false,
      }
    );
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateMenuToggle() {
    if(this.state.windowWidth > 767) {
      this.setState({ toggleMenu: true, toggleMenuBg: false });
    } else {
      this.setState({ toggleMenu: false, toggleMenuBg: false });
    }
  }
  
  updateWindowDimensions() {
    this.setState({ 
      windowWidth: window.innerWidth
    });
    this.updateMenuToggle();
  }

  render() {
    return (
      <div className="main-header">
        <div className="main-header-container">
          <img src={pathPrefix+"/static/icons/menu.svg"} onClick={this.handleMenuClick} className="main-header-menu-icon" />
          <Link className="menu-logo" to="/">
            <img
              src='/static/fitato-logo.png'
              title='Fitato'
              alt="fitato"
            />
          </Link>
          <div className="menu-links" style={(this.state.toggleMenu) ? displayBlock : displayNone}>
            <Link to="/partners" exact className="menu-link-item" activeClassName="menu-link-item-active" onClick={this.handleMenuClick}>Facilities</Link>
            <Link to="/pricing" exact className="menu-link-item" activeClassName="menu-link-item-active" onClick={this.handleMenuClick}>Pricing</Link>
            <a href="http://blog.fitato.fit" target="_blank" rel="noopener" className="menu-link-item">Blog</a>
            <Link to="/contact" exact className="menu-link-item" activeClassName="menu-link-item-active" onClick={this.handleMenuClick}>Contact</Link>
            <Link to="/faq" exact className="menu-link-item" activeClassName="menu-link-item-active" onClick={this.handleMenuClick}>FAQ</Link>
            <a href="https://fitato.app.link/6CvYxe0W9L" className="button" onClick={this.handleMenuClick}
              style={
                {
                  marginRight: '0',
                  marginTop: '0',
                  marginBottom: '0',
                  marginLeft:'10px',
                }
              }>
              Download now
            </a>
          </div>
        </div>
        <div className="mobile-menu-bg" style={(this.state.toggleMenuBg) ? displayBlock : displayNone} onClick={this.handleBgClick}></div>
      </div>
    )
  }
}

export default Header



