import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'

import Header from '../components/header'
import Footer from '../components/footer'
import './index.css'

const Layout = ({ children, data }) => (
  <div>
    <Helmet
      title={data.site.siteMetadata.title}
      meta={[
        { name: 'description', content: 'Fitato is Your One Pass to 250+ Best Gyms & Fitness Studios in Pune. Access All Gyms, Dance, Yoga, Crossfit and Swimming Near You.' },
      ]}
    >
      <link rel="icon" type="image/png" href='static/fitato-favicon.png' />
      <link href="https://fonts.googleapis.com/css?family=Muli:700,700i|Roboto:400,400i,700,700i" rel="stylesheet" />
      <link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
    </Helmet>
    <Header siteTitle={data.site.siteMetadata.title} />
    <div className="main-header-placeholder" />
    <div>
      {children()}
    </div>
    <Footer />
  </div>
)

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



